.Content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 30px;
  /* overflow-y: scroll; */

  /* text */
  color: white;
  font-family: Raleway, sans-serif;
  font-weight: 300;
  line-height: 1.8;
  font-size: 16px;
  text-align: left;
}

.Content h5 {
  font-size: 20px;
  font-weight: 600;
  /* TODO: ask if italic is better than text shadow */
  font-style: italic;
  /* text-shadow: -3px 3px rgba(0,0,0,0.3); */
}

.Content .content-body {
  /* z-index: 20; */
  height: 35vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

#AboutContent .link {
  cursor: pointer;
  color: white;
  white-space: nowrap;
  text-decoration: underline;
}


