#ContentPanel {
  width: 100%;
  height: 100%;
}

.door-wrapper {
  height: 60vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.door-text {
  color: white;
  font-family: 'Encode Sans Semi Condensed', sans-serif;
  font-weight: 100;
  font-size: 25px;
}

.door-title {
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* TODO: scale up when hovered */
.construction-message {
  position: absolute;
  left: 0;
  right: 0;
  top: 37%;
  margin: auto;
  -webkit-transform: rotate(-20deg);
      -ms-transform: rotate(-20deg);
          transform: rotate(-20deg);
  text-align: center;

  /* text */
  color: white;
  font-family: Satisfy;
  font-size: 60px;
  text-shadow: -3px 3px rgba(0,0,0,0.5);
}

.content-elem {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}


